import { Component, OnInit, AfterViewInit, ViewChild, HostBinding } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { IonSlides, MenuController } from '@ionic/angular';
import { AuthenticationService } from '../DESARROLLO/services/Authentication.service';

@Component({
  selector: 'app-walkthrough',
  templateUrl: './walkthrough.page.html',
  styleUrls: [
    './styles/walkthrough.page.scss',
    './styles/walkthrough.shell.scss',
    './styles/walkthrough.responsive.scss'
  ]
})
export class WalkthroughPage implements OnInit, AfterViewInit {
  titulo1: any;
  idempresa: any;
  foto0 = '/api/image2s/5f146aa48caa41db981e6830';
  nombre0: any;
  nombre1: any;
  foto2 = '/api/image2s/5f146aa48caa41db981e6830';
  apifoto: any;
  cintia = '/api/image2s/5f146aa48caa41db981e6830';
  slidesOptions: any = {
    zoom: {
      toggle: false // Disable zooming to prevent weird double tap zomming on slide images
    }
  };

  @ViewChild(IonSlides, { static: true }) slides: IonSlides;

  @HostBinding('class.first-slide-active') isFirstSlide = true;

  @HostBinding('class.last-slide-active') isLastSlide = false;

  constructor(public menu: MenuController  , public translateService: TranslateService ,
    private authenticationService: AuthenticationService   ) {   this.apifoto = this.authenticationService.getapifoto(); }

  ngOnInit(): void {
    this.menu.enable(false);

 
      this.idempresa =  this.authenticationService.getempresa()

      this.authenticationService.getRegsintok(this.idempresa + '/todos', 'empresas', 0).then((data) => {
        let datat: any = [];
        datat = data;

     this.titulo1 = datat[0].titulo0;
     this.foto0 = datat[0].foto0;
     this.foto2 = datat[0].foto2;
     this.nombre0 = datat[0].nombre;
     this.nombre1 = datat[0].nombrecorto;

     document.documentElement.style.setProperty('--ion-color-primary',  datat[0].color1);
     document.documentElement.style.setProperty('--ion-color-secondary',  datat[0].color2);
     document.documentElement.style.setProperty('--ion-color-tertiary',  datat[0].color3);
     document.documentElement.style.setProperty('--ion-color-medium',  datat[0].color4);

  }, (err) => {
    this.authenticationService.presentAlert(err.error , 'Precaución' , '');
  });


  }

  ngAfterViewInit(): void {
    // ViewChild is set
    this.slides.isBeginning().then(isBeginning => {
      this.isFirstSlide = isBeginning;
    });
    this.slides.isEnd().then(isEnd => {
      this.isLastSlide = isEnd;
    });

    // Subscribe to changes
    this.slides.ionSlideWillChange.subscribe(changes => {
      this.slides.isBeginning().then(isBeginning => {
        this.isFirstSlide = isBeginning;
      });
      this.slides.isEnd().then(isEnd => {
        this.isLastSlide = isEnd;
      });
    });
  }

  skipWalkthrough(): void {
    // Skip to the last slide
    this.slides.length().then(length => {
      this.slides.slideTo(length);
    });
  }
}
